/**菜数据 */
export interface DataDish {
    Factor: number;
    Id: number;
    Level: number;
    Region: string;
    /**正确名字 */
    CorrectName: string;
    /**错误名字 */
    WrongName: string;
}
/**地区菜配置 */
export interface ConfigRegionDish {
    Id: number;
    /**中文名 */
    City: string;
    /**拼音名 */
    NamePinYin: string;
    /**是否限免 */
    IsFree: boolean,
    /**是否成功 */
    IsSuccess: boolean,
    /**星星等级 */
    StarsLevel: number,
    /**通关人数 */
    SuccessCount: number,
}
/**地区排行配置 */
export interface ConfigRegionRanking {
    Id: number,
    /**中文名 */
    City: string;
    /**星星数量 */
    StarsNumber: number,
    /**排行名次 */
    Ranking?: number,
}
/**每日奖励状态配置 */
export enum ConfigSidebarState {
    /**知道了 */
    Ok,
    /**待领取 */
    Get,
    /**已领取 */
    Other,
}
/**分享奖励状态配置 */
export enum ConfigShareState {
    /**邀请 */
    Share,
    /**领取 */
    Get,
}

